html {
  box-sizing: border-box;
  background-color: #8FE98E;
  font-family: "Roboto", sans-serif;
  overflow-x: hidden;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

a {
  text-decoration: none;
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #8FE98E;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.header .fondo-logo {
  width: 18%;
  height: 1.8rem;
  position: absolute;
  background-color: #00D335;
  z-index: 0;
  margin-left: -2%;
  border-radius: 20px;
}
.header .logo {
  z-index: 10;
  margin-left: 20px;
  background-color: #8FE98E;
  width: 10rem;
}
.header .logo a {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #01252E;
  font-size: 1.5rem;
  font-weight: 700;
}
.header .menu {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: #00D335;
  border-radius: 20px;
  width: 60%;
  margin-right: -2%;
}
.header .menu .navbar a {
  margin: 0 12px;
  font-size: 1.5rem;
  font-weight: 700;
  color: #01252E;
}
.header .menu .navbar a:hover {
  border-bottom: 1px solid #01252E;
}
.header .menu .login {
  background-color: #8FE98E;
  width: 8rem;
  text-align: center;
  margin-right: 10px;
}
.header .menu .login a {
  font-size: 1.5rem;
  font-weight: 700;
  color: #01252E;
}

.inicio {
  display: flex;
  margin: 5rem 0 0;
  justify-content: center;
  align-items: center;
}
.inicio .about {
  color: white;
  display: flex;
  background-color: #01252E;
  border-radius: 20px;
  height: 60%;
  flex-direction: column;
  justify-content: center;
  text-align: right;
  padding: 1rem 1rem;
  margin: 0 3rem;
  width: 40%;
}
.inicio .about a {
  width: 25%;
  margin: 1rem 0 0 75%;
  text-align: center;
  color: #01252E;
  font-weight: 700;
  background-color: #00D335;
  border-radius: 5px;
}
.inicio .imagen-about {
  width: 60%;
}
.inicio .imagen-about img {
  width: 100%;
}

.productos {
  margin: 2.5rem 0;
  background-image: url(../../../build/img/mando-2.webp);
  background-position: center;
  background-size: cover;
  height: 400px;
}
.productos .buscador {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.productos .buscador h2 {
  font-size: 3rem;
  color: #adacac;
}
.productos .buscador .search-bar input {
  border: none;
  font-weight: 700;
  height: 2.5rem;
  width: 25rem;
  border-radius: 0.75em 0 0 0.75em;
  outline: none;
  margin-right: 0;
}
.productos .buscador .search-bar button {
  border: 1px solid white;
  cursor: pointer;
  height: 2.5rem;
  width: 2.5rem;
  background: #2b8141;
  margin-left: -4px;
  border-radius: 0 0.75em 0.75em 0/0 1.5em 1.5em 0;
}
.productos .boton-catalogo {
  display: flex;
  justify-content: center;
  margin-top: 5rem;
}
.productos .boton-catalogo button {
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  border-radius: 10px;
  height: 2.5rem;
  background: #525252;
}

.blog {
  margin: 3rem 0;
  height: 600px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.blog .imagen-blog {
  background-image: url(../../../build/img/blog-2.webp);
  background-position: center;
  background-size: cover;
  width: 40%;
  height: 100%;
  margin: 0 2rem;
}
.blog .contenedor-blog {
  background-color: #01252E;
  width: 50%;
  height: 70%;
  margin: 0 2rem;
  border-radius: 20px;
}